'use client';

import React, { useEffect, useMemo, useState } from 'react';
import { useParams, useRouter } from 'next/navigation';
import { createClient } from '@/lib/supabaseBrowser';
import { Topbar } from '@/components/Topbar';
import { Button } from '@/components/Button';
import { Card } from '@/components/Card';

type WorkoutUIType = 'rodagem' | 'progressivo' | 'alternado';
type Intensity = 'leve' | 'moderado' | 'forte';
type BlockIntensity = Intensity | 'livre';

type Student = {
  id: string;
  name: string;
  email?: string | null;
  p1k_sec_per_km?: number | null;
};

type ProgressivePhase = {
  id: string;
  distanceKm: number;
  intensity: Intensity;
};

type WorkoutBlock = {
  index: number;
  segment_type: 'warmup' | 'main' | 'cooldown';
  label: string;
  distance_km: number;
  intensity: BlockIntensity;
  pace_min_sec_per_km?: number | null;
  pace_max_sec_per_km?: number | null;
  hint_text?: string | null;
};

function clampNumber(n: number, min: number, max: number) {
  if (Number.isNaN(n)) return min;
  return Math.min(max, Math.max(min, n));
}

function formatError(err: unknown): string {
  const msg = err instanceof Error ? err.message : String(err ?? '');
  if (msg.includes('workouts_cooldown_km_check')) {
    return 'Desaquecimento inválido. Verifique se o desaquecimento está ativado e se a distância é maior que 0.';
  }
  if (msg.includes('workouts_warmup_km_check')) {
    return 'Aquecimento inválido. Verifique se o aquecimento está ativado e se a distância é maior que 0.';
  }
  if (msg.includes('workouts_template_type_check')) {
    return 'Tipo de treino inválido. Selecione Rodagem, Progressivo ou Alternado.';
  }
  if (msg.includes('Cannot coerce the result to a single JSON object')) {
    return 'Não foi possível carregar o aluno (retorno inesperado). Verifique RLS e se o aluno existe.';
  }
  return msg;
}

function fmtDateBR(d: Date) {
  const dd = String(d.getDate()).padStart(2, '0');
  const mm = String(d.getMonth() + 1).padStart(2, '0');
  return `${dd}/${mm}`;
}

function secToMinKm(sec?: number | null) {
  if (!sec || sec <= 0) return '—';
  const m = Math.floor(sec / 60);
  const s = sec % 60;
  return `${m}:${String(s).padStart(2, '0')}`;
}

function rangeForIntensity(p1k?: number | null, intensity?: Intensity) {
  if (!p1k || p1k <= 0) return null;

  // Heurística simples por enquanto (MVP)
  // leve: +90s a +150s
  // moderado: +45s a +90s
  // forte: +0s a +30s
  const base = p1k;
  if (intensity === 'leve') return { min: base + 90, max: base + 150 };
  if (intensity === 'moderado') return { min: base + 45, max: base + 90 };
  if (intensity === 'forte') return { min: base + 0, max: base + 30 };
  return null;
}

function uid() {
  return Math.random().toString(16).slice(2);
}

export default function NewWorkoutPage() {
  const router = useRouter();
  const params = useParams<{ id: string }>();
  const studentId = params?.id;

  const supabase = useMemo(() => createClient(), []);

  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const [student, setStudent] = useState<Student | null>(null);

  const [workoutType, setWorkoutType] = useState<WorkoutUIType>('rodagem');

  const [title, setTitle] = useState('');
  const [notes, setNotes] = useState('');

  // Aquecimento
  const [warmupEnabled, setWarmupEnabled] = useState(true);
  const [warmupKm, setWarmupKm] = useState<number>(1);

  // Desaquecimento
  const [cooldownEnabled, setCooldownEnabled] = useState(true);
  const [cooldownKm, setCooldownKm] = useState<number>(1);

  // Rodagem / Alternado (blocos principais simples)
  const [mainDistanceKm, setMainDistanceKm] = useState<number>(5);
  const [mainIntensity, setMainIntensity] = useState<Intensity>('leve');

  // Progressivo (fases)
  const [progressivePhases, setProgressivePhases] = useState<ProgressivePhase[]>([
    { id: uid(), distanceKm: 2, intensity: 'leve' },
    { id: uid(), distanceKm: 2, intensity: 'moderado' },
    { id: uid(), distanceKm: 1, intensity: 'forte' },
  ]);

  // Alternado (intervalos)
  const [altReps, setAltReps] = useState<number>(6);
  const [altWorkKm, setAltWorkKm] = useState<number>(0.5);
  const [altWorkIntensity, setAltWorkIntensity] = useState<Intensity>('forte');
  const [altRestKm, setAltRestKm] = useState<number>(0.5);

  useEffect(() => {
    if (!studentId) return;

    let alive = true;

    async function load() {
      try {
        setLoading(true);
        setError(null);

        const { data, error: err } = await supabase
          .from('students')
          .select('id,name,email,p1k_sec_per_km')
          .eq('id', studentId)
          .single();

        if (err) throw err;
        if (!alive) return;

        setStudent(data as Student);
      } catch (e) {
        if (!alive) return;
        setError(formatError(e));
        setStudent(null);
      } finally {
        if (!alive) return;
        setLoading(false);
      }
    }

    load();
    return () => {
      alive = false;
    };
  }, [studentId, supabase]);

  const computed = useMemo(() => {
    const blocks: WorkoutBlock[] = [];
    let idx = 0;

    const p1k = student?.p1k_sec_per_km ?? null;

    if (warmupEnabled) {
      blocks.push({
        index: idx++,
        segment_type: 'warmup',
        label: 'Aquecimento',
        distance_km: clampNumber(warmupKm, 0.5, 50),
        intensity: 'livre',
        hint_text: 'Ritmo confortável.',
      });
    }

    if (workoutType === 'rodagem') {
      const r = rangeForIntensity(p1k, mainIntensity);
      blocks.push({
        index: idx++,
        segment_type: 'main',
        label: 'Bloco principal',
        distance_km: clampNumber(mainDistanceKm, 0.1, 200),
        intensity: mainIntensity,
        pace_min_sec_per_km: r?.min ?? null,
        pace_max_sec_per_km: r?.max ?? null,
        hint_text: r ? `Ritmo sugerido: ${secToMinKm(r.min)}–${secToMinKm(r.max)} /km` : null,
      });
    }

    if (workoutType === 'progressivo') {
      progressivePhases.forEach((ph) => {
        const r = rangeForIntensity(p1k, ph.intensity);
        blocks.push({
          index: idx++,
          segment_type: 'main',
          label: `Progressivo (${ph.intensity})`,
          distance_km: clampNumber(ph.distanceKm, 0.1, 50),
          intensity: ph.intensity,
          pace_min_sec_per_km: r?.min ?? null,
          pace_max_sec_per_km: r?.max ?? null,
          hint_text: r ? `Ritmo sugerido: ${secToMinKm(r.min)}–${secToMinKm(r.max)} /km` : null,
        });
      });
    }

    if (workoutType === 'alternado') {
      const workRange = rangeForIntensity(p1k, altWorkIntensity);
      for (let i = 0; i < clampNumber(altReps, 1, 50); i++) {
        blocks.push({
          index: idx++,
          segment_type: 'main',
          label: `Tiro ${i + 1}`,
          distance_km: clampNumber(altWorkKm, 0.1, 50),
          intensity: altWorkIntensity,
          pace_min_sec_per_km: workRange?.min ?? null,
          pace_max_sec_per_km: workRange?.max ?? null,
          hint_text: workRange ? `Ritmo sugerido: ${secToMinKm(workRange.min)}–${secToMinKm(workRange.max)} /km` : null,
        });
        blocks.push({
          index: idx++,
          segment_type: 'main',
          label: `Recuperação ${i + 1}`,
          distance_km: clampNumber(altRestKm, 0.1, 50),
          intensity: 'livre',
          hint_text: 'Recuperação leve.',
        });
      }
    }

    if (cooldownEnabled) {
      blocks.push({
        index: idx++,
        segment_type: 'cooldown',
        label: 'Desaquecimento',
        distance_km: clampNumber(cooldownKm, 0.5, 50),
        intensity: 'livre',
        hint_text: 'Finalize soltando.',
      });
    }

    const total_km = blocks.reduce((sum, b) => sum + (b.distance_km || 0), 0);

    // Sugestão de título: só usa quando o campo está vazio
    const suggestedTitle =
      workoutType === 'rodagem'
        ? 'Rodagem'
        : workoutType === 'progressivo'
          ? 'Progressivo'
          : 'Alternado';

    return { blocks, total_km, suggestedTitle };
  }, [
    student?.p1k_sec_per_km,
    warmupEnabled,
    warmupKm,
    cooldownEnabled,
    cooldownKm,
    workoutType,
    mainDistanceKm,
    mainIntensity,
    progressivePhases,
    altReps,
    altWorkKm,
    altWorkIntensity,
    altRestKm,
  ]);

  const titleValue = title.trim().length ? title : '';

  async function upsertWorkout(status: 'draft' | 'shared') {
    if (!studentId) throw new Error('Aluno inválido.');

    const now = new Date();
    const trainerId = (student as any)?.trainer_id ?? undefined; // caso esteja vindo no select em outra versão

    const payload: any = {
      student_id: studentId,
      trainer_id: trainer?.id ? undefined : trainerId,
      status,
      template_type: workoutType,
      include_warmup: warmupEnabled,
      warmup_km: warmupEnabled ? clampNumber(warmupKm, 0.5, 50) : null,
      include_cooldown: cooldownEnabled,
      cooldown_km: cooldownEnabled ? clampNumber(cooldownKm, 0.5, 50) : null,
      template_params: {
        title: titleValue || computed.suggestedTitle,
        notes: notes?.trim() || null,
      },
      blocks: computed.blocks,
      total_km: computed.total_km,
      share_slug: status === 'shared' ? `${studentId}-${Date.now()}` : null,
      version: 1,
      created_at: now.toISOString(),
      updated_at: now.toISOString(),
    };

    const { error: err } = await supabase.from('workouts').insert(payload);
    if (err) throw err;

    return payload;
  }

  async function handleSaveDraft() {
    try {
      setSaving(true);
      setError(null);
      await upsertWorkout('draft');
      router.back();
    } catch (e) {
      setError(formatError(e));
    } finally {
      setSaving(false);
    }
  }

  async function handleShare() {
    try {
      setSaving(true);
      setError(null);
      const inserted = await upsertWorkout('shared');
      const slug = inserted?.share_slug;
      if (slug) router.push(`/w/${slug}`);
      else router.back();
    } catch (e) {
      setError(formatError(e));
    } finally {
      setSaving(false);
    }
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-[#0B1B14] text-white">
        <Topbar title="Criar Treino" backHref={`/students/${studentId}`} />
        <div className="max-w-3xl mx-auto px-4 py-6">Carregando...</div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-[#0B1B14] text-white">
      <Topbar title="Criar Treino" backHref={`/students/${studentId}`} />

      <div className="max-w-3xl mx-auto px-4 py-6 space-y-4">
        {error ? (
          <div className="rounded-xl border border-red-500/40 bg-red-950/30 px-4 py-3 text-red-200">
            {error}
          </div>
        ) : null}

        <Card>
          <div className="text-xs tracking-widest text-white/60">ALUNO</div>
          <div className="text-xl font-semibold">{student?.name || '—'}</div>
          <div className="text-white/70">
            Ritmo P1K: {secToMinKm(student?.p1k_sec_per_km)} min/km
          </div>
        </Card>

        <Card>
          <div className="text-sm font-semibold mb-2">Título do treino</div>
          <input
            className="w-full rounded-xl bg-white/5 px-4 py-3 outline-none border border-white/10 focus:border-white/30"
            value={titleValue}
            onChange={(e) => setTitle(e.target.value)}
            placeholder="Ex: Rodagem leve"
          />
          <div className="mt-2 text-sm text-white/70">
            Distância total estimada: {computed.total_km.toFixed(1)} km
          </div>
        </Card>

        <Card>
          <div className="text-sm font-semibold mb-3">Tipo de Treino</div>
          <div className="flex gap-2">
            {(['rodagem', 'progressivo', 'alternado'] as WorkoutUIType[]).map((t) => (
              <button
                key={t}
                className={[
                  'px-4 py-2 rounded-full border text-sm',
                  workoutType === t
                    ? 'bg-emerald-400 text-black border-emerald-300'
                    : 'bg-transparent text-white border-white/20',
                ].join(' ')}
                onClick={() => setWorkoutType(t)}
                type="button"
              >
                {t === 'rodagem' ? 'Rodagem' : t === 'progressivo' ? 'Progressivo' : 'Alternado'}
              </button>
            ))}
          </div>
        </Card>

        <Card>
          <div className="text-sm font-semibold mb-3">Estrutura</div>

          <div className="flex items-center justify-between">
            <div className="flex items-center gap-2">
              <span className="material-symbols-outlined text-white/70">local_fire_department</span>
              <div className="font-semibold">Aquecimento</div>
            </div>

            <label className="inline-flex items-center cursor-pointer">
              <input
                type="checkbox"
                className="sr-only peer"
                checked={warmupEnabled}
                onChange={(e) => setWarmupEnabled(e.target.checked)}
              />
              <div className="w-11 h-6 bg-white/20 peer-focus:outline-none rounded-full peer peer-checked:bg-emerald-500 relative after:content-[''] after:absolute after:top-0.5 after:left-0.5 after:bg-white after:h-5 after:w-5 after:rounded-full after:transition-all peer-checked:after:translate-x-5" />
            </label>
          </div>

          {warmupEnabled ? (
            <div className="mt-3 grid grid-cols-[1fr_auto_1fr] gap-3 items-center">
              <div>
                <div className="text-xs tracking-widest text-white/60">DISTÂNCIA</div>
                <input
                  className="mt-1 w-full rounded-xl bg-white/5 px-4 py-3 outline-none border border-white/10 focus:border-white/30"
                  value={String(warmupKm)}
                  onChange={(e) => setWarmupKm(Number(e.target.value.replace(',', '.')))}
                  inputMode="decimal"
                />
              </div>
              <div className="pt-6 text-white/60">km</div>
              <div>
                <div className="text-xs tracking-widest text-white/60">RITMO</div>
                <div className="mt-1 w-full rounded-xl bg-white/5 px-4 py-3 border border-white/10 text-white/80">
                  Livre
                </div>
              </div>
            </div>
          ) : null}
        </Card>

        <Card>
          <div className="text-sm font-semibold mb-3">Blocos Principais</div>

          {workoutType === 'rodagem' ? (
            <div className="grid grid-cols-[1fr_auto_1fr] gap-3 items-center">
              <div>
                <div className="text-xs tracking-widest text-white/60">DISTÂNCIA DO TRECHO</div>
                <input
                  className="mt-1 w-full rounded-xl bg-white/5 px-4 py-3 outline-none border border-white/10 focus:border-white/30"
                  value={String(mainDistanceKm)}
                  onChange={(e) => setMainDistanceKm(Number(e.target.value.replace(',', '.')))}
                  inputMode="decimal"
                />
              </div>
              <div className="pt-6 text-white/60">km</div>
              <div>
                <div className="text-xs tracking-widest text-white/60">INTENSIDADE</div>
                <div className="mt-2 flex gap-2">
                  {(['leve', 'moderado', 'forte'] as Intensity[]).map((it) => (
                    <button
                      key={it}
                      className={[
                        'px-4 py-2 rounded-full border text-sm',
                        mainIntensity === it
                          ? 'bg-emerald-400 text-black border-emerald-300'
                          : 'bg-transparent text-white border-white/20',
                      ].join(' ')}
                      onClick={() => setMainIntensity(it)}
                      type="button"
                    >
                      {it === 'leve' ? 'Leve' : it === 'moderado' ? 'Moderado' : 'Forte'}
                    </button>
                  ))}
                </div>
              </div>
            </div>
          ) : null}

          {workoutType === 'progressivo' ? (
            <div className="space-y-3">
              {progressivePhases.map((ph, idx) => (
                <div key={ph.id} className="rounded-xl border border-white/10 bg-white/5 p-3">
                  <div className="flex items-center justify-between">
                    <div className="text-sm font-semibold">Fase {idx + 1}</div>
                    <button
                      type="button"
                      className="text-white/60 hover:text-white"
                      onClick={() => setProgressivePhases((arr) => arr.filter((x) => x.id !== ph.id))}
                      title="Remover fase"
                    >
                      <span className="material-symbols-outlined">delete</span>
                    </button>
                  </div>

                  <div className="mt-3 grid grid-cols-[1fr_auto_1fr] gap-3 items-center">
                    <div>
                      <div className="text-xs tracking-widest text-white/60">DISTÂNCIA</div>
                      <input
                        className="mt-1 w-full rounded-xl bg-white/5 px-4 py-3 outline-none border border-white/10 focus:border-white/30"
                        value={String(ph.distanceKm)}
                        onChange={(e) => {
                          const v = Number(e.target.value.replace(',', '.'));
                          setProgressivePhases((arr) =>
                            arr.map((x) => (x.id === ph.id ? { ...x, distanceKm: v } : x)),
                          );
                        }}
                        inputMode="decimal"
                      />
                    </div>
                    <div className="pt-6 text-white/60">km</div>
                    <div>
                      <div className="text-xs tracking-widest text-white/60">INTENSIDADE</div>
                      <div className="mt-2 flex gap-2">
                        {(['leve', 'moderado', 'forte'] as Intensity[]).map((it) => (
                          <button
                            key={it}
                            className={[
                              'px-4 py-2 rounded-full border text-sm',
                              ph.intensity === it
                                ? 'bg-emerald-400 text-black border-emerald-300'
                                : 'bg-transparent text-white border-white/20',
                            ].join(' ')}
                            onClick={() =>
                              setProgressivePhases((arr) =>
                                arr.map((x) => (x.id === ph.id ? { ...x, intensity: it } : x)),
                              )
                            }
                            type="button"
                          >
                            {it === 'leve' ? 'Leve' : it === 'moderado' ? 'Moderado' : 'Forte'}
                          </button>
                        ))}
                      </div>
                    </div>
                  </div>
                </div>
              ))}

              <Button
                onClick={() =>
                  setProgressivePhases((arr) => [...arr, { id: uid(), distanceKm: 1, intensity: 'moderado' }])
                }
              >
                + Adicionar fase
              </Button>
            </div>
          ) : null}

          {workoutType === 'alternado' ? (
            <div className="space-y-3">
              <div className="grid grid-cols-2 gap-3">
                <div>
                  <div className="text-xs tracking-widest text-white/60">REPETIÇÕES</div>
                  <input
                    className="mt-1 w-full rounded-xl bg-white/5 px-4 py-3 outline-none border border-white/10 focus:border-white/30"
                    value={String(altReps)}
                    onChange={(e) => setAltReps(Number(e.target.value))}
                    inputMode="numeric"
                  />
                </div>
                <div>
                  <div className="text-xs tracking-widest text-white/60">INTENSIDADE DO TIRO</div>
                  <div className="mt-2 flex gap-2">
                    {(['leve', 'moderado', 'forte'] as Intensity[]).map((it) => (
                      <button
                        key={it}
                        className={[
                          'px-4 py-2 rounded-full border text-sm',
                          altWorkIntensity === it
                            ? 'bg-emerald-400 text-black border-emerald-300'
                            : 'bg-transparent text-white border-white/20',
                        ].join(' ')}
                        onClick={() => setAltWorkIntensity(it)}
                        type="button"
                      >
                        {it === 'leve' ? 'Leve' : it === 'moderado' ? 'Moderado' : 'Forte'}
                      </button>
                    ))}
                  </div>
                </div>
              </div>

              <div className="grid grid-cols-[1fr_auto_1fr] gap-3 items-center">
                <div>
                  <div className="text-xs tracking-widest text-white/60">DISTÂNCIA DO TIRO</div>
                  <input
                    className="mt-1 w-full rounded-xl bg-white/5 px-4 py-3 outline-none border border-white/10 focus:border-white/30"
                    value={String(altWorkKm)}
                    onChange={(e) => setAltWorkKm(Number(e.target.value.replace(',', '.')))}
                    inputMode="decimal"
                  />
                </div>
                <div className="pt-6 text-white/60">km</div>
                <div>
                  <div className="text-xs tracking-widest text-white/60">DISTÂNCIA DA RECUPERAÇÃO</div>
                  <div className="mt-1 flex items-center gap-2">
                    <input
                      className="w-full rounded-xl bg-white/5 px-4 py-3 outline-none border border-white/10 focus:border-white/30"
                      value={String(altRestKm)}
                      onChange={(e) => setAltRestKm(Number(e.target.value.replace(',', '.')))}
                      inputMode="decimal"
                    />
                    <span className="text-white/60">km</span>
                  </div>
                </div>
              </div>
            </div>
          ) : null}
        </Card>

        <Card>
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-2">
              <span className="material-symbols-outlined text-white/70">ac_unit</span>
              <div className="font-semibold">Desaquecimento</div>
            </div>

            <label className="inline-flex items-center cursor-pointer">
              <input
                type="checkbox"
                className="sr-only peer"
                checked={cooldownEnabled}
                onChange={(e) => setCooldownEnabled(e.target.checked)}
              />
              <div className="w-11 h-6 bg-white/20 peer-focus:outline-none rounded-full peer peer-checked:bg-emerald-500 relative after:content-[''] after:absolute after:top-0.5 after:left-0.5 after:bg-white after:h-5 after:w-5 after:rounded-full after:transition-all peer-checked:after:translate-x-5" />
            </label>
          </div>

          {cooldownEnabled ? (
            <div className="mt-3 grid grid-cols-[1fr_auto_1fr] gap-3 items-center">
              <div>
                <div className="text-xs tracking-widest text-white/60">DISTÂNCIA</div>
                <input
                  className="mt-1 w-full rounded-xl bg-white/5 px-4 py-3 outline-none border border-white/10 focus:border-white/30"
                  value={String(cooldownKm)}
                  onChange={(e) => setCooldownKm(Number(e.target.value.replace(',', '.')))}
                  inputMode="decimal"
                />
              </div>
              <div className="pt-6 text-white/60">km</div>
              <div>
                <div className="text-xs tracking-widest text-white/60">RITMO</div>
                <div className="mt-1 w-full rounded-xl bg-white/5 px-4 py-3 border border-white/10 text-white/80">
                  Livre
                </div>
              </div>
            </div>
          ) : null}
        </Card>

        <Card>
          <div className="text-sm font-semibold mb-2">Observações (opcional)</div>
          <textarea
            className="w-full min-h-[96px] rounded-xl bg-white/5 px-4 py-3 outline-none border border-white/10 focus:border-white/30"
            value={notes}
            onChange={(e) => setNotes(e.target.value)}
            placeholder="Ex.: manter cadência, hidratação, etc."
          />
        </Card>

        <div className="flex gap-3">
          <Button onClick={handleSaveDraft} disabled={saving} variant="secondary">
            {saving ? 'Salvando...' : 'Salvar Rascunho'}
          </Button>
          <Button onClick={handleShare} disabled={saving}>
            Compartilhar
          </Button>
        </div>

        <div className="text-xs text-white/40">
          Atualizado em {fmtDateBR(new Date())}
        </div>
      </div>
    </div>
  );
}
